export default {
	data() {
		return {
			orderInfo: getApp().globalData.orderInfo,
			isShow: getApp().globalData.isShow,
			priceType: 0,
			type: 1,
			list: [],
			items: [
				{
					value: 0,
					name: '普通',
					introduction: '',
					price: getApp().globalData.orderInfo.jiage
				},
				{
					value: 1,
					name: '加急',
					introduction: '',
					price: getApp().globalData.orderInfo.jiaji_fee
				}
			],
			current: 0
		}
	},
	onLoad: function (option) {
		const {
			id
		} = option
		console.log(id)
	},
	methods: {
		radioChange: function(evt) {
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[i].value === evt.target.value) {
					this.current = i;
					break;
				}
			}
		},
		handleTab (type) {
			this.type = type
		},
		auth (auth){
			const params = {
				'auth_code': auth
			}
			this.$api.auth(params).then(res => {
				console.log(res)
			})
		},
        onKeyInput: function(event) {
            this.mobile = event.target.value
        },
		handleSubmit (){
			if (!this.mobile) {
				uni.showToast({
					title: '请输入手机号',
					icon: 'none'
		　　　　　})
				return false;
			}
			const params = {
				fkdbh: this.orderInfo.jdsbh,
				user_id: getApp().globalData.userId,
				jiaji: this.current,
				mobile: this.mobile,
				total: this.current ===0 ? this.orderInfo.total : this.orderInfo.jiaji_total
			}
			this.$api.pay(params).then(res => {
				my.tradePay({
				  // 调用统一收单交易创建接口（alipay.trade.create），获得返回字段支付宝交易号trade_no
					tradeNO: res.trade_no,
					success: (r) => {
						if (r.resultCode === "9000" || r.resultCode === "8000"){
							this.$api.payOver({trade_no:res.trade_no}).then(over => {
								uni.switchTab({
									url: `/pages/record/index`
								});
							})
						}
						console.log(r)
						// my.alert({
						//   content: JSON.stringify(r),
						// });
					},
					fail: (res) => {
						my.alert({
						  content: JSON.stringify(res),
						});
					}
				});
			})
		}
	}
}
